import React, { useRef } from "react";
import { useGLTF } from "@react-three/drei";

export function Solidarity(props) {
  const { nodes, materials } = useGLTF("/glossarySelected/solidarity.glb");
  return (
    <group {...props} dispose={null}>
      <group //position={[10.75, 0.76, 4.67]}
      >
        <mesh
          castShadow
          receiveShadow
          geometry={nodes.Sphere160.geometry}
          material={materials["glass.004"]}
        />
        <mesh
          castShadow
          receiveShadow
          geometry={nodes.Sphere160_1.geometry}
          material={materials["glass.001"]}
        />
        <mesh
          castShadow
          receiveShadow
          geometry={nodes.Sphere160_2.geometry}
          material={materials.outline}
        />
      </group>
    </group>
  );
}
export default Solidarity;
useGLTF.preload("/glossarySelected/solidarity.glb");
